.btn-close {
	position: absolute;
	content: '';
	top: -36px;
	right: 0;
	width: 16px;
	height: 16px;
	cursor: pointer;
	transition: .5s ease-out;

	@media only screen and (max-width: 768px) {
		right: 15px;
		top: 15px; }


	&::after {
		position: absolute;
		content: '';
		width: 16px;
		height: 2px;
		background-color: #c4c4c4;
		left: 0;
		top: 8px;
		transform: rotate(-45deg); }

	&::before {
		position: absolute;
		content: '';
		width: 16px;
		height: 2px;
		background-color: #c4c4c4;
		left: 0;
		top: 8px;
		transform: rotate(45deg); }

	&:hover {

		opacity: 0.5;
		transition: .5s ease-out; } }





.overlay-popup {
