.manager-image-right {
	position: absolute;
	top: 170px;
	right: 300px;

	z-index: 4;

	&.right-image-move {
		right: 1350px;
		transition: .2s ease-out;

		@media only screen and (max-width: 1750px) {
			right: 1000px; }

		@media only screen and (max-width: 1200px) {
			right: 850px; } }

	@media only screen and (max-width: 768px) {
		position: static;
		width: 360px;
		height: auto;
		margin-top: -60px; } }

.manager-image-left {
	position: absolute;
	top: 100px;
	left: 250px;
	z-index: 4;
	transition: .2s ease-out;

	&.left-image-move {
		left: 1400px;
		transition: .2s ease-out;

		@media only screen and (max-width: 1750px) {
			left: 1000px; }

		@media only screen and (max-width: 1200px) {
			left: 850px; } }

	@media only screen and (max-width: 768px) {
		position: static;
		width: 360px;
		height: auto;
		margin-top: -60px; } }


.manager-content {
	padding: 30px 35px 52px 20px;
	width: 330px;
	text-align: left;
	position: relative;
	min-height: 398px;
	align-self: flex-start;

	@media only screen and (max-width: 768px) {
		min-height: 50px; }

	&.min-height {
		min-height: 50px; }


	@media only screen and (max-width: 768px) {
		width: 95%;
		margin-bottom: 135px;
		align-self: center; }

	&-blue {
		max-width: 100%;
		background-color: $middle-blue; }

	&-orange {
		order: 1;
		background-color: $midle-orange;

		@media only screen and (max-width: 768px) {
			order: -1; } }


	&-text {
		min-height: 172px;
		margin-bottom: 18px;
		margin-top: 0;
		line-height: 20px;

		@media only screen and (max-width: 1200px) {
			min-height: 50px; }

		@media only screen and (max-width: 768px) {
			min-height: 50px;
			margin-bottom: 30px; } }

	&-descr {
		font-family: "BebasNeueBold", Arial, sans-serif;
		font-size: 24px;
		color: $white;
		line-height: 28px;
		margin-top: 16px;
		margin-bottom: 13px; }


	&-salary {
		font-family: "BebasNeueBold", Arial, sans-serif;
		font-size: 32px;
		color: $white;
		line-height: 32px;
		margin-top: 14px;
		margin-bottom: 0; }

	.btn {
		position: absolute;
		bottom: 0;
		right: 50%;
		transform: translate(50%, 50%);
		font-family: "BebasNeueRegular", Arial, sans-serif;
		font-size: 26px;
		color: $white;
		background-color: transparent;
		border: none;
		width: 225px;
		height: 65px;
		cursor: pointer;





		&-back-left {
			text-align: right;
			bottom: -60px;
			width: auto;
			opacity: 0;
			visibility: hidden;
			transition: .2s ease-out;
			padding-left: 35px;
			background: url('../img/arrow-back.svg') no-repeat center;
			background-position: 0 24px;





			&.show {
				display: block;
				visibility: visible;
				opacity: 1;
				transition: .2s ease-out;

				@media only screen and (max-width: 1200px) {
					display: none; } } }



		&-back-right {
			text-align: left;
			bottom: -60px;
			width: auto;
			opacity: 0;
			visibility: hidden;
			transition: .2s ease-out;
			padding-left: 35px;
			background: url('../img/arrow-back.svg') no-repeat center;
			background-position: 0 24px;

			&.show {
				visibility: visible;
				opacity: 1;
				transition: .2s ease-out;

				@media only screen and (max-width: 1200px) {
					display: none; } } }

		&-left {
			background: url(../img/btn_left.svg) no-repeat center;

			&:hover {
				opacity: .9; }


			&.btn-left-active {
				transform: none;
				right: 50px;
				bottom: 60px; } }




		&-right {
			background: url(../img/btn_right.svg) no-repeat center;

			&:hover {
				opacity: .9; }

			&.btn-right-active {
				transform: none;
				right: 50px;
				bottom: 45px; } } } }




