@import url(normalize.css);

@import url(fonts.css);

html,
body {
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

p {
  font-family: "FiraSansRegular", Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.resume-form {
  display: none;
  position: fixed;
  color: #000000;
  background-color: #ffffff;
  z-index: 1000;
  width: 620px;
  padding: 50px 40px 30px 40px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.resume-form h2 {
  font-family: "BebasNeueBold", Arial, sans-serif;
  font-size: 26px;
  line-height: 34px;
  margin-top: 0;
  margin-bottom: 12px;
}

.resume-form-text {
  color: #000000;
  font-family: "FiraSansBold", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 30px;
}

.resume-form input[type="text"] {
  width: 100%;
  padding: 18px 20px 18px 23px;
  color: #7f7f7f;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid #E5E5E5;
  margin-bottom: 10px;
}

.resume-form .offer-button {
  margin-top: 20px;
  width: 100%;
}

.resume-form .button-gray {
  margin-top: 0;
}

.resume-form-conditions {
  display: none;
  position: fixed;
  color: #000000;
  background-color: #ffffff;
  z-index: 1000;
