}

.button-wrapper-conditions {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
}

.form-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.btn-close {
  position: absolute;
  content: '';
  top: -36px;
  right: 0;
  width: 16px;
  height: 16px;
  cursor: pointer;
  transition: .5s ease-out;
}

.btn-close::after {
  position: absolute;
  content: '';
  width: 16px;
  height: 2px;
  background-color: #c4c4c4;
  left: 0;
  top: 8px;
  transform: rotate(-45deg);
}

.btn-close::before {
  position: absolute;
  content: '';
  width: 16px;
  height: 2px;
  background-color: #c4c4c4;
  left: 0;
  top: 8px;
  transform: rotate(45deg);
}

.btn-close:hover {
  opacity: 0.5;
  transition: .5s ease-out;
}

.overlay-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: .8;
  z-index: 1;
  display: none;
}

.resume-form-show {
  display: block;
  transition: .8s ease-out;
}

.red {
