    margin-bottom: 30px;
  }

  .press-slider-item {
    flex-direction: column;
    min-height: auto;
    justify-content: flex-start;
  }

  .press-slider-item-orange {
    flex-direction: column;
    min-height: auto;
    justify-content: flex-start;
  }

  .press-slider-date {
